export default {
	data() {
		return {
			floordata: []
		};
	},
	created() {
		uni.request({
			url: 'https://api-hmugo-web.itheima.net/api/public/v1/home/floordata',
			success: (res) => {
				// console.log(res)
				this.floordata = res.data.message
			}
		});
	}

}
